<template>
  <top-nav> 商品详情({{ id }}) </top-nav>
  <div class="main-box">
    <div class="top">
      <van-image width="95%" height="auto" :src="detail.cover_url" />
      <van-card
        :num="detail.stock"
        :price="detail.price + '.00'"
        :desc="detail.description"
        :title="detail.title"
      >
        <template #tags>
          <van-tag plain type="danger">畅销</van-tag>
          <van-tag plain type="danger">新书</van-tag>
        </template>
      </van-card>
      <van-tabs v-model:active="active" class="tabNav">
        <van-tab title="商品详情">
          <div class="goods_details" v-html="detail.details"></div>
        </van-tab>
        <!--商品评论-->
        <van-tab title="详细评论">
          <div class="comment" v-for="item in detail.comments" :key="item.id">
            <div class="comment_top">
              <van-image
                width="50"
                height="50"
                :src="item.user.avatar_url"
              />
              <div class="comment_top_rignt">
                <p>{{item.user.name}}</p>
                <p>{{item.created_at}}</p>
              </div>
            </div>

            <div class="comment_bottom">
              <p>{{item.content}}</p>
            </div>
          </div>
        </van-tab>
        <van-tab title="相似推荐">
          <goods-list :goods="like_books"></goods-list>
        </van-tab>
      </van-tabs>
    </div>
    <div class="bottom">
      <van-action-bar>
        <van-action-bar-icon icon="chat-o" text="客服" />
        <van-action-bar-icon icon="shop-o" text="店铺" />
        <van-action-bar-button
          color="#3f77ac"
          type="warning"
          text="加入购物车"
        />
        <van-action-bar-button color="#034e94" type="danger" text="立即购买" />
      </van-action-bar>
    </div>
  </div>
</template>

<script>
import TopNav from "../../components/TopNav.vue";
import GoodsList from "../../components/GoodList.vue";
import { useRoute, useRouter } from "vue-router";
import { ref, reactive, onMounted, toRefs } from "vue";
import { getDateil } from "../../network/detail";
import {addCart} from '../../network/cart'
import { Toast } from "vant";
export default {
  name: "Detail",
  components: { TopNav, GoodsList },
  setup() {
    const router = useRouter()
    const route = useRoute();
    const id = ref(0);
    const active = ref(0);
    const books = reactive({
      detail: {},
      like_books: [],
    });
    onMounted(async () => {
      id.value = route.query.id;
      await getDateil(id.value).then((res) => {
        console.log(res);
        books.detail = res.goods;
        books.like_books = res.like_goods;
        console.log(res.goods.comments);
        console.log(res.like_goods);
      });
    });


    return {
      id,
      ...toRefs(books),
      active,

    };
  },
};
</script>

<style lang="scss" scoped>
.main-box {
  margin-top: 50px;

  margin-bottom: 60px;
  font-size: 16px;
}
.van-action-bar {
  bottom: 65px;
  z-index: 99;
}
.van-card {
  text-align: left;
  background-color: #fff;
  margin-bottom: 10px;
  border-radius: 10px;
  width: 95%;
  margin: 2.5%;
  font-size: 14px;
}
.van-image {
  width: 95%;
  border-radius: 10px !important;
  height: 100%;
  margin: 2.5%;
}
.tabNav {
  width: 95%;
  margin-left: 2.5%;
  border-radius: 10px;
  margin-bottom: 120px;
}
.goods_details {
  width: 95%;

  border-radius: 10px;
  background-color: #fff;
  margin: 10px 2.5% 0 2.5%;
}
.comment{
  background-color: #fff;
  border-radius: 10px;
  margin: 10px 2.5% 0 2.5%;
  display: flex;
  flex-direction: column;
  text-align: left;
  .comment_top{
    align-items: center;
    justify-content: flex-start;
    display: flex;
    flex: 1;
    color: rgb(121, 121, 121);
  }
  .comment_bottom{
    flex: 1;
    line-height: 21px;
    padding: 10px;
  }
}
</style>
